.main-nav {
    display: flex;
    flex-direction: column;
    position: relative;
    min-width: 320px;
    width: 100%;
    z-index: 10;
}

.main-nav--opened .main-nav__toggle-button {
    background-image: url("../img/index/menu.svg");
    background-repeat: no-repeat;
    background-position: center;
}

.main-nav--closed .main-nav__toggle-button {
    background-image: url("../img/index/menu-close.svg");
    background-repeat: no-repeat;
    background-position: center;
}

.main-nav--active {
    display: flex;
    flex-direction: column;
}

.main-nav__logo-toggle-wrap {
    display: flex;
    justify-content: center;
}

.main-nav__toggle {
    position: absolute;
    top: 32px;
    right: 31px;
    //justify-self: flex-end;
}

.main-nav--nojs .main-nav__toggle {
    display: none;
}


.main-nav__container {
    display: flex;
    flex-direction: column;
}

