.main-nav__list {
    display: flex;
    flex-direction: column;
    margin: 0;
    padding: 0;
}

@media (max-width: @mobile-width) {
    .main-nav {
        background-color: #FFFFFF;
    }

    .main-nav--nojs .main-nav__container {
        position: static;
    }

    .main-nav--closed .main-nav__container {
        display: none;
    }
    
    .main-nav--opened .main-nav__container {
        display: flex;
        position: absolute;
        content: "";
        z-index: 10;
        width: 100%;
        height: min-content;
        top: 79px;
        left: 0;    
    }    
}

@media (min-width: @tablet-width) {
    .main-nav {
        min-width: 664px;
        flex-direction: row;
        flex-wrap: wrap;
        //justify-content: space-between;
    }

    .main-nav__logo-toggle-wrap {
        //width: 322px;
        justify-content: flex-start;
        margin-left: 50px;
        //width: 48%;    
    }

    .main-nav__container {
        justify-self: flex-end;
        flex-direction: row;
        flex-wrap: wrap;
        margin-right: 50px;
        margin-left: auto;
    }

    .main-nav__list {
        flex-direction: row;
    }
}

@media (min-width: @desktop-width) {
    .main-nav {
        width: 1050px;
    }

    .main-nav__logo-toggle-wrap {
        width: 150px;
        margin-left: 0;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }

    .main-nav__container {
        width: 1050px;
        justify-content: space-between;
        margin-right: 0;
    }
}